body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-size: 0;
  background-color: whitesmoke;
}

.nav {
  position: fixed;
  width: 100%;
  top: 0;
}

.body {
  width: 100%;
  height: 100%;
  padding: 60px 0 0 0;
}

div {
  font-size: 1rem;
}

.card {
  margin: 30px 15%;
  padding: 20px;
  width: 70%;
  height: 350px;
  background-color: rgb(248, 250, 223);
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 3rem;
  font-weight: bolder;
  border-radius: 30px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
