* {
  box-sizing: border-box;
}
nav {
  display: flex;
  width: 100%;
  background: antiquewhite;
  font-size: 1rem;
  align-items: center;
  height: 60px;
  box-sizing: border-box;
  padding: 0px;
}

.left {
  display: flex;
  width: 20%;
  min-width: 220px;
  height: 100%;
  padding: 0 10px;
  align-items: center;

  text-decoration: none;
}

.left * {
  margin: 0 10px;
  font-weight: bolder;
  text-decoration: none;
  color: black;
  font-size: 1.5rem;
  white-space: nowrap;
}
.main_Icon {
  width: 40px;
  height: 40px;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 100%;
}

input {
  margin: 0;
  width: 200px;
  width: calc(100% - 50px);
  height: 80%;
  background-color: white;
  border-color: bisque;
  border-radius: 10px;
}
.search_Icon {
  width: 30px;
  height: 30px;
  margin: 0 10px;
}

.right {
  display: flex;
  padding: 0 15px 0 0;
  width: 30%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.right button {
  width: 40%;
  height: 70%;
  height: calc (100% - 10px);
  margin: 0;
  background-color: rgb(250, 235, 215);
  border-radius: 15px;
  margin: 0 5px;
}

.hamburger {
  width: 40px;
  height: 40px;
  margin-left: 10px;
}

@media screen and (max-width: 768px) {
  .left > div {
    display: none;
  }
  .left {
    width: 75px;
    min-width: 75px;
  }
  .center {
    width: calc(100% - 315px);
  }

  .right {
    min-width: 240px;
  }
}
